/** @module components/FolderSelector */

import React from 'react';
import ReactDOM from 'react-dom';
import { observer, Observer, inject, Provider } from 'mobx-react';
import AIOTree, { Popconfirm, Spin, Icon } from 'react-aiot';
import Breadcrumb from './Breadcrumb';
import { i18n } from '../util';
import { BasicStore, createUnorganizedNode } from '../store';
import rmlOpts from 'rmlopts';
import classNames from 'classnames';
import $ from 'jquery';
import { getSnapshot } from 'mobx-state-tree';

@inject('tree')
@observer
export class FolderSelector extends React.Component {
    
    static defaultProps = {
        fetch: true, // Set to false if you want to set the selectable tree by yourself (store)
        nullable: false,
        editable: true,
        onSelect: undefined,
        onNodeInit: undefined,
        ignoreChildNodes: false,
        selected: undefined, // Use this only without input
        input: undefined, // Input is optional, use selected for initial selected id
        disabled: '' // comma seperated list of types which are disabled
    }
    
    constructor({ input, disabled, selected }) {
        super(...arguments);
        
        this.inputObject = input || document.createElement('input');
        
        // Initial selection
        selected !== undefined && (this.inputObject.value = selected);
        
        this.state = {
            isTreeBusy: false,
            visible: false,
            disabled: (disabled ? ('' + disabled).split(',') : []).map(i => +i)
        };
    }
    
    /**
     * Set the selected id to the input field so it can be updated through the properties.
     */
    componentWillReceiveProps({ input, selected }) {
        if (!input && selected !== this.props.selected) {
            this.handleSelect(selected);
        }
    }
    
    /**
     * Initially set the value of the input field and breadcrumb path.
     * 
     * @internal
     */
    async componentDidMount() {
        const val = this.getInputValue(),
            { tree } = this.props;
        if (!this.props.nullable && (!val || val < 0)) {
            this.inputObject.value = -1;
        }
        
        this.setState({ isTreeBusy: true });
        if (this.props.fetch) {
            try {
                // Check if global rml store holds already a tree
                const mainStore = this.getMainStore();
                if (mainStore) {
                    tree.setTree(mainStore.getTreeSnapshot());
                }else{
                    await tree.fetchTree();
                }
            } catch (e) {
                console.log(e);
            }
        }
        
        // Disable nodes
        const { disabled } = this.state;
        if (disabled.length) {
            const fnDisabled = node => {
                if (disabled.indexOf(+node.properties?.type) > -1) {
                    node.setter(n => (n.className = 'rml-deactivated'));
                }
            };
            tree.nodes(fnDisabled);
            tree.nodes(fnDisabled, true);
        }
        
        // Modify this tree
        this.setState({ isTreeBusy: false });
        
        // Preselect
        const item = this.handleSelect(val);
        this.props.onNodeInit && this.props.onNodeInit(item, this.inputObject);
    }
    
    getInputValue = () => {
        const val = this.inputObject.value;
        return val === '' ? val : +val;
    }
    
    getMainStore = () => /* global rml */ rml.store.tree.length && rml.store
    
    /**
     * A node item should be an observer (mobx).
     */
    onTreeNodeRender = (createTreeNode, TreeNode, node) => {
        return <Observer key={ node.id }>{ () => createTreeNode(node) }</Observer>;
    }
    
    handleSelect = id => {
        let item;
        const previousValue = this.getInputValue();
        if (id === '') { // nullable
            item = this.props.tree.selected;
            item && item.setter(node => (node.selected = false));
            item = null;
        }else{
            item = this.props.tree.getTreeItemById(id, false);
            
            if (!item) {
                return this.handleSelect(+rmlOpts.rootId);
            }else{
                item.setter(node => (node.selected = true));
            }
        }
        
        this.inputObject.value = id;
        $(this.inputObject).data('node', item);
        this.setState({ visible: false });
        previousValue !== id && this.props.onSelect && this.props.onSelect(item, this.inputObject);
        return item;
    }
    
    handleVisibleChange = visible => {
        if (this._nullableNextVisibleChange) {
            visible = false;
        }
        this.setState({ visible });
        this._nullableNextVisibleChange = false;
    }
    
    handleSelectNull = e => {
        this._nullableNextVisibleChange = true;
        this.handleSelect('');
    }
    
    renderPopup() {
        const { staticTree, tree } = this.props.tree,
            { ignoreChildNodes } = this.props;
        return ( <div style={{ maxWidth: 500, maxHeight: 400 }}>
            <AIOTree rootId={ +rmlOpts.rootId } staticTree={ staticTree } tree={ tree.length > 0 ? tree : [] } isResizable={ false } isFullWidth
                creatable={{ buttons: {} }} toolbar={{ buttons: {} }} onSelect={ this.handleSelect } renderItem={ this.onTreeNodeRender }
                headline={ <span style={{ paddingRight: 5 }}>{ i18n('folders') }</span> } noFoldersTitle={ i18n('noFoldersTitle') }
                ignoreChildNodes={ ignoreChildNodes } noFoldersDescription="" noSearchResult={ i18n('noSearchResult') } theme="wordpress"
                autoFocusSearchInput { ...this.state } />
        </div> );
    }
    
    render() {
        const { isTreeBusy, visible } = this.state,
            { className, style, editable, nullable, tree: { breadcrumb, selected } } = this.props;
        return ( <Popconfirm visible={ visible && editable } onVisibleChange={ this.handleVisibleChange } overlayClassName="rml-folder-selector" theme="dark"
            placement="bottomLeft" title={ this.renderPopup() } okText={i18n('ok')} cancelText={i18n('cancel')} icon={ <span/> }>
            <div className={ classNames('rml-folder-edit', className, {
                'rml-deactivated': !editable
            }) } style={ style }>
                <Spin spinning={ isTreeBusy }>
                    <Breadcrumb path={ breadcrumb } closeable={ selected && editable && nullable } onClose={ this.handleSelectNull } />
                </Spin>
            </div>
        </Popconfirm> );
    }
    
}

/**
 * Create the folder selector in a given target.
 * 
 * @param {HTMLElement} target
 * @param {HTMLElement} input An input where the selected id is stored (usually input[type="hidden"])
 * @param {object} props The properties for FolderSelector control
 * @returns {object} The created store
 */
export default function createFolderSelector(target, input, props) {
    const store = BasicStore.create({
        staticTree: [createUnorganizedNode()]
    });
    
    ReactDOM.render(( <Provider tree={ store }>
        <FolderSelector input={ input } { ...props } />
    </Provider> ), target);
    return store;
}