/** @module others/renderSortMenu */

import React from 'react';
import ReactDOM from 'react-dom';
import { Menu, message } from 'react-aiot';
import { i18n, IS_DARKMODE, hooks, textDots } from '../util';
import { observer, inject, Provider } from 'mobx-react';
import { DashIcon, Modal } from '../components';
import FolderBox, { FolderBoxHeader } from '../components/FolderBox';
import { FolderSelector } from '../components/FolderSelector';
import { BasicStore, createUnorganizedNode } from '../store';
import { getSnapshot } from 'mobx-state-tree';

const { Item, SubMenu, Divider, ItemGroup } = Menu;

hooks.register('folder/children/order/name_asc', (children, setChildren) => {
    setChildren(children.sort((a,b) => (a.title > b.title) ? 1 : ((b.title > a.title) ? -1 : 0)));
}).register('folder/children/order/name_desc', (children, setChildren) => {
    setChildren(children.sort((a,b) => (a.title > b.title) ? -1 : ((b.title > a.title) ? 1 : 0)));
}).register('folder/children/order/id_asc folder/children/order/original', (children, setChildren) => {
    setChildren(children.sort((a,b) => (a.id > b.id) ? 1 : ((b.id > a.id) ? -1 : 0)));
}).register('folder/children/order/id_desc', (children, setChildren) => {
    setChildren(children.sort((a,b) => (a.id > b.id) ? -1 : ((b.id > a.id) ? 1 : 0)));
});

/**
 * An element rendering sortables for the popup menu.
 * 
 * @returns React.Element[]
 */
const createSortables = (sortables, select, selectText) => Object.keys(sortables).map(key => (
    <Item key={ key }>{ sortables[key] } { select === key && (<strong>({ selectText })</strong>) }</Item>
) );

/**
 * Apply an order to a tree node object.
 * 
 * @this AppTree
 */
async function applyOrder(selected, key, automatically) {
    const hide = message.loading(i18n('sortLoadingText', { name: selected.title }));
    await selected.applyChildrenOrder(key, automatically);
    hide();
}

/**
 * When clicking on a menu item in the order menu popup.
 * 
 * @this AppTree
 */
async function handleClick({ key, keyPath }) {
    const path = keyPath.reverse(),
        { selectedId, selected } = this.props.store,
        _applyOrder = applyOrder.bind(this);
    if (path[0] === 'dnd') { // Apply sorting once
        this.stateRefs.handleSortClick();
    }else if (path[0] === 'manual') {
        this.setState({ rearrangeBoxId: selectedId });
    }else if (path[0] === 'applyOnce') {
        _applyOrder(selected, key);
    }else if (path[0] === 'applyAutomatically') {
        _applyOrder(selected, key, true);
    }else if (key === 'reset') {
        _applyOrder(selected, 'original');
    }else if (key === 'resetAutomatically') {
        _applyOrder(selected, 'deactivate');
    }else if (key === 'applyReindex') {
        _applyOrder(selected, 'reindex');
    }
}

/**
 * Render the order menu.
 * 
 * @type React.Element
 */
export default function() {
    const { store } = this.props,
        { selectedId, selected } = store,
        sortables = store.treeSortables,
        isFolder = selectedId > 0;

    return <Menu onClick={ handleClick.bind(this) } theme={ (IS_DARKMODE ? 'dark' : 'light') }>
        <Item key="dnd">
            { i18n('sortByDnd') }
        </Item>
        { isFolder && ( <Item key="manual">
            { i18n('sortByManual', { name: textDots(selected.title) }) }
        </Item> ) }
        { isFolder && ( <Divider /> ) }
        { isFolder && ( <ItemGroup title={ (<span>{ i18n('subfolders') } ({ textDots(selected.title) })</span>) }>
            { !!selected.lastSubOrderBy && ( <Item key="reset">
                { i18n('resetOrder') }
            </Item> ) }
            { !selected.subOrderAutomatically ? ( <SubMenu key="applyOnce" title={ i18n('applyOrderOnce') }>
                { sortables && createSortables(sortables, selected.lastSubOrderBy, i18n('last')) }
            </SubMenu> ) : ( <Item key="resetAutomatically">
                { i18n('deactivateOrderAutomatically') }
            </Item> ) }
            <SubMenu key="applyAutomatically" title={ i18n('applyOrderAutomatically') }>
                { sortables && createSortables(sortables, selected.subOrderAutomatically && selected.lastSubOrderBy, i18n('latest')) }
            </SubMenu>
            { !!selected.lastSubOrderBy && (<Item key="applyReindex">
                { i18n('reindexOrder') }
            </Item>) }
        </ItemGroup> ) }
    </Menu>;
}

/**
 * Show a rearrange box for the selected folder id.
 * 
 * @property {int} id The id of the folder
 * @extends React.Component
 */
@inject('store')
@observer
export class RearrangeBox extends React.Component {
    constructor(props) {
        super(props);
        
        this.state = {
            nextId: undefined,
            loading: false
        };
    }
    
    componentWillReceiveProps({ id }) {
        if (id !== this.props.id) {
            // Create parent store
            this.parentStore = BasicStore.create({
                staticTree: [createUnorganizedNode()]
            });
            
            // Create sibling store
            this.siblingStore = BasicStore.create({
                staticTree: [createUnorganizedNode()]
            });
        }
    }
    
    /**
     * Create the sibling tree.
     */
    handleSelect = parent => {
        const { siblingStore } = this,
            tree = getSnapshot(parent ? parent.childNodes : this.props.store.tree);
        tree.forEach(n => (n.childNodes = []));
        siblingStore.setTree(tree);
        const { selected } = siblingStore;
        selected && selected.setter(n => (n.selected = false)); // Reset selection
    }
    
    handleSave = async () => {
        this.setState({ loading: true });
        
        try {
            const { store } = this.props,
                { id } = this.item,
                { parentStore, siblingStore } = this,
                oldItem = store.getTreeItemById(id),
                props = {
                    id,
                    oldIndex: oldItem.parentArray.indexOf(oldItem),
                    parentFromId: oldItem.parentId,
                    parentToId: parentStore.selected ? parentStore.selectedId : store.rootId,
                    nextId: siblingStore.selectedId || NaN,
                    doFinally: false
                };
                
            // Get newIndex
            const parentTo = store.getTreeItemById(props.parentToId),
                newIndexRelative = parentTo ? parentTo.childNodes : store.tree,
                newIndexA = newIndexRelative.filter(({ id }) => id !== oldItem.id).map(({ id }, idx) => id === props.nextId ? "" + idx : null).filter(Boolean).map(i => +i);
            props.newIndex = !isNaN(props.nextId) && newIndexA.length ? newIndexA[0] : newIndexRelative.length;
            
            // Do the sorting
            this.props.onSort(props);
        } finally {
            this.setState({ loading: false });
        }
    }
    
    /**
     * Save the editing item to the instance and prepare the first sibling tree.
     */
    handleNodeInit = (itemId, ...args) => {
        const { nextSibling, parentArray } = this.item = this.parentStore.getTreeItemById(itemId, false),
            id = nextSibling ? nextSibling.id : undefined;
        this.setState({ nextId: id }, () => {
            const tree = getSnapshot(parentArray), { siblingStore, parentStore } = this;
            tree.forEach(n => (n.childNodes = []));
            siblingStore.setTree(tree);
            parentStore.getTreeItemById(this.item.id).setter(n => (n.$visible = false));
            siblingStore.getTreeItemById(this.item.id).setter(n => (n.$visible = false));
            id !== undefined && siblingStore.getTreeItemById(id).setter(n => (n.selected = true));
        });
    }
    
    render() {
        let modalContent, title;
        const selected = this.props.store.getTreeItemById(this.props.id, false);
        if (selected) {
            const selectedId = selected.parentId > 0 ? selected.parentId : '';
            title = ( <FolderBoxHeader icon={ ( <DashIcon name="sort" /> ) } folder={ selected } /> );
            modalContent = ( <FolderBox folder={ selected } header={ false }>
                <form>
                    <h3>{ i18n('parent') }</h3>
                    <Provider tree={ this.parentStore }>
                        <FolderSelector selected={ selectedId } nullable disabled="4" onSelect={ this.handleSelect } onNodeInit={ (...args) => this.handleNodeInit(this.props.id, ...args) } />
                    </Provider>
                    <h3>{ i18n('beforeThisNode') }</h3>
                    <Provider tree={ this.siblingStore }>
                        <FolderSelector fetch={ false } nullable disabled="4" ignoreChildNodes />
                    </Provider>
                    <p className="description">{ i18n('beforeThisNodeInfo') }</p>
                </form>
            </FolderBox> );
        }
        
        return ( <Modal visible={ !!modalContent } title={ title } onOk={ this.handleSave } onCancel={ this.props.onClose }
            cancelText={ i18n('cancel') } okText={ i18n('save') } zIndex="160001" confirmLoading={ this.state.loading }>
            { modalContent }
        </Modal> );
    }
}