/** @module store/TreeNode */

import { ajax, hooks } from '../util';
import { types, flow, getParentOfType, getRoot, getParent } from "mobx-state-tree";
import $ from 'jquery';
import { uuid, getTreeParentById } from 'react-aiot';

/**
 * The store holding general data for folders. The properties are read-only.
 * 
 * @see React AIOT TreeNode documentation for properties and defaults
 * @class TreeNode
 */
const TreeNode = types.model('RMLTreeNode', {
    id: types.identifier,
    hash: '',
    className: types.optional(types.frozen()),
    icon: types.optional(types.frozen()),
    iconActive: types.optional(types.frozen()),
    childNodes: types.optional(types.array(types.late(() => TreeNode))),
    title: types.string,
    count: 0,
    attr: types.optional(types.frozen()),
    isTreeLinkDisabled: false,
    checked: false,
    selected: false,
    $busy: false,
    $busyOrder: false,
    $droppable: true,
    $visible: true,
    $rename: false,
    $create: types.optional(types.frozen()),
    contentCustomOrder: 0,
    forceCustomOrder: false,
    // Content order
    lastOrderBy: '',
    orderAutomatically: false,
    // Subfolder order
    lastSubOrderBy: '',
    subOrderAutomatically: false,
    //searchSelected: false,
    //expandedState: true,
    //displayChildren: true,
    //selectedIds: [],
    //onRenameClose: undefined,
    //onAddClose: undefined,
    //onSelect: undefined,
    //onNodePressF2: undefined,
    //onExpand: undefined,
    //onUlRef: undefined
    properties: types.optional(types.frozen()),
    isQueried: true
}).views(self => ({
    get parentArray() {
        return getParent(self);
    },
    
    get nextSibling() {
        const parentArray = self.parentArray,
            indexOf = parentArray.indexOf(self),
            nextOf = indexOf + 1;
        return indexOf > -1 ? parentArray[nextOf] : undefined;
    },
    
    get path() {
        let result = [], parent = self;
        while (parent) {
            result.push(parent);
            try {
                parent = getParentOfType(parent, TreeNode);
            }catch (e) {
                parent = undefined;
            }
        }
        return result.reverse();
    },
    
    get parentId() {
        const root = getRoot(self);
        return getTreeParentById(self.id, root.tree, root.rootId);
    }
})).actions(self => ({
    /**
     * Update this node attributes.
     * 
     * @param {function} callback The callback with one argument (node draft)
     * @param {boolean} [setHash] If true the hash node is changed so a rerender is forced
     * @memberof module:store/TreeNode~TreeNode
     * @instance
     */
    setter(callback, setHash = false) {
        callback(self);
        setHash && (self.hash = uuid());
    },
    
    /**
     * Update the checked flag.
     * 
     * @memberof module:store/TreeNode~TreeNode
     * @instance
     */
    toggleChecked() {
        self.checked = !self.checked;
    },
    
    /**
     * Rename folder.
     * 
     * @param {string} inputValue The new name
     * @returns {object} Server response
     * @throws Error
     * @memberof module:store/TreeNode~TreeNode
     * @instance
     * @async
     */
    setName: flow(function*(inputValue) {
        self.setter(node => { node.$busy = true });
        
        try {
            let result;
            const { id, name, cnt, children, ...rest } = result = yield ajax('folders/' + self.id, {
                method: 'PUT',
                data: {
                    name: inputValue
                }
            });
            self.setter(node => {
                node.title = name;
                node.properties = $.merge(node.properties, rest);
                node.$busy = false;
            });
            return result;
        } catch(e) {
            self.setter(node => { node.$busy = false }, self.id);
            throw e;
        }
    }),
    
    /**
     * Apply an order to the folder content.
     * 
     * @param {string} id The sortable id
     * @param {boolean} [automatically=false] If true the order is applied automatically if new files are added to the folder
     * @memberof module:store/TreeNode~TreeNode
     * @instance
     * @async
     */
    applyOrder: flow(function*(id, automatically = false) {
        self.setter(node => { node.$busyOrder = true });
        
        try {
            const result = yield ajax('folders/' + self.id + '/content/sortables', {
                method: 'POST',
                data: {
                    id,
                    automatically
                }
            });
            
            if (result) {
                id !== 'reindex' && id !== 'last' && (self.contentCustomOrder = 1, self.lastOrderBy = id);
                automatically && (self.orderAutomatically = true);
                id === 'original' && (self.contentCustomOrder = 0, self.orderAutomatically = false);
                id === 'deactivate' && (self.orderAutomatically = false);
            }
        }catch(e) {
            throw e;
        }finally{
            self.setter(node => { node.$busyOrder = false });
        }
    }),
    
    /**
     * Apply an order to the childrens.
     * 
     * @param {string} [id] The sortable id - if not set the childNodes are sorted again by the last local known orderby
     * @param {boolean} [automatically=false] If true the order is applied automatically if new folders are added as subfolders
     * @memberof module:store/TreeNode~TreeNode
     * @instance
     * @async
     * @since 4.4
     */
    applyChildrenOrder: flow(function*(id, automatically = false) {
        id && self.setter(node => { node.$busyOrder = true });
        
        try {
            if (id) {
                const result = yield ajax('folders/' + self.id + '/sortables', {
                    method: 'POST',
                    data: {
                        id,
                        automatically
                    }
                });
                
                if (result) {
                    id !== 'reindex' && id !== 'last' && (self.lastSubOrderBy = id);
                    automatically && (self.subOrderAutomatically = true);
                    id === 'original' && (self.lastSubOrderBy = '');
                    (id === 'deactivate' || id === 'original') && (self.subOrderAutomatically = false);
                }
            }
            
            /**
             * This action is called when subfolders of a folder are successfully
             * ordered by a criterium. You have to implement the .setter() for the new
             * children's order.
             * 
             * @event module:util/hooks#folder/children/order/$id
             * @param {TreeNode[]} children The children of this folder, you have to apply .compare() because it is not reloaded from server
             * @param {function} setChildren A function where you pass one argument: childNodes with new sorting
             * @this module:store/TreeNode~TreeNode
             * @since 4.4.0
             */
            hooks.call('folder/children/order/' + (id || self.lastSubOrderBy), [self.childNodes, newChildNodes => self.setter(n => (n.childNodes.replace(newChildNodes)), true)], self);
        }catch(e) {
            throw e;
        }finally{
            id && self.setter(node => { node.$busyOrder = false });
        }
    }),
    
    /**
     * Toggle the view of this folder.
     * 
     * @param boolean state
     * @memberof module:store/TreeNode~TreeNode
     * @instance
     * @since 4.0.9
     */
    visible(state) {
        self.$visible = state;
    },
    
    /**
     * Permanently delete folder.
     * 
     * @returns {string|int} The parent id
     * @throws Error
     * @memberof module:store/TreeNode~TreeNode
     * @instance
     * @async
     */
    trash: flow(function*() {
        self.setter(node => { node.$busy = true });
        
        try {
            yield ajax('folders/' + self.id, {
                method: 'DELETE'
            });
            self.visible(false);
        }catch(e) {
            throw e;
        }finally{
            self.setter(node => { node.$busy = false });
        }
    })
}));

export default TreeNode;